import request from '../request'


// 获取列表
export function listSeries(data) {
    return request({
        url: '/api/series/listSeries',
        method: 'post',
        data
    })
}

// 新增
export function saveSeries(data) {
    return request({
        url: '/api/series/saveSeries',
        method: 'post',
        data
    })
}

// 修改
export function updateSeries(data) {
    return request({
        url: '/api/series/updateSeries',
        method: 'post',
        data
    })
}

// 删除
export function deleteSeriesById(params) {
    return request({
        url: '/api/series/deleteSeriesById',
        method: 'get',
        params: params
    });
}
